import React, { useState, useEffect, useRef } from "react";
import Layout from "../../components/Layout/Layout";
import axios from "axios";
import { toast } from "react-toastify";
import { Modal } from "antd";
import EditCategory from "../../components/element/EditCategory";
import { useReactToPrint } from "react-to-print";
import { useDarkMode } from "../../context/DarkModeContext";
import { useAuth } from "../../context/auth";
import DataTable, { createTheme } from "react-data-table-component";

const CategoryForm = () => {
  const [category, setCategory] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selected, setSelected] = useState(null);
  const [updateName, setUpdateName] = useState("");

  const componentRef = useRef();
  const [auth, setAuth] = useAuth();
  const { isDarkMode } = useDarkMode();

  // States
  const [selectedRows, setSelectedRows] = useState([]);
  const [records, setRecords] = useState([]); //table data
  const [filterRecords, setFilterRecords] = useState([]); //table data

  // get all categories
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get(`/api/category/get-category`);
      if (data?.success) {
        setRecords(data?.category);
        setFilterRecords(data?.category);
      }
    } catch (error) {
      console.log(error);
      toast.error("Erro ao carregar categorias.");
    }
  };

  useEffect(() => {
    getAllCategory();
  }, []);

  const handleUpdate = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put(
        `/api/category/update-category/${selected._id}`,
        { name: updateName }
      );
      if (data.success) {
        toast.success(`Categoria ${updateName} atualizada com sucesso`);
        setSelected(null);
        setUpdateName("");
        setIsModalOpen(false);
        getAllCategory();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      toast.error(error.response.data.message);
    }
  };

  // delete category
  const handleDelete = async (pId) => {
    try {
      const { data } = await axios.delete(
        `/api/category/delete-category/${pId}`
      );
      if (data.success) {
        toast.success(`Categoria ${category.name} apagada com sucesso`);
        getAllCategory();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      toast.error(error.response.data.message);
    }
  };

  const columns = [
    {
      name: "Categoria",
      selector: (row) => row.name,
      sortable: true,
      width: "400px",
    },
    {
      name: "Ações",
      cell: (row) => (
        <>
          <button
            className="btn btn-sm btn-primary mr-2"
            onClick={() => {
              setSelected(row);
              setUpdateName(row.name);
              setIsModalOpen(true);
            }}
          >
            <i className="fas fa-edit" />
          </button>
          <button
            className="btn btn-sm btn-danger"
            onClick={() => handleDelete(row._id)}
          >
            <i className="fas fa-trash" />
          </button>
        </>
      ),
      button: true,
      width: "80px",
    },
  ];

  createTheme("dark", {
    text: {
      primary: "#fff",
      secondary: "#fff",
    },
    background: {
      default: "#292e33",
    },
    context: {
      background: "#3e4854",
      text: "#FFFFFF",
    },
    divider: {
      default: "#fff",
    },
    striped: {
      default: "#343a40",
      text: "#FFFFFF",
    },
    action: {
      button: "rgba(0,0,0,.54)",
      hover: "rgba(0,0,0,.08)",
      disabled: "rgba(0,0,0,.12)",
    },
  });

  const customStyles = {
    rows: {
      style: {
        // minHeight: "72px", // override the row height
      },
    },
    headCells: {
      style: {
        textTransform: "uppercase",
        fontSize: "18px",
        fontWeight: "bold",
      },
    },
    cells: {
      style: {
        textTransform: "uppercase",
        fontSize: "15px",
      },
    },
  };

  const handleFilter = (e) => {
    const searchTerm = e.target.value.toLowerCase();

    const newData = filterRecords.filter(
      (row) =>
        row.name.toLowerCase().includes(searchTerm)
    );

    setRecords(newData);
  };

  // Função para selecionar as linhas
  const handleRowSelected = (state) => {
    setSelectedRows(state.selectedRows);
  };

  // Função para imprimir
  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
    documentTitle: "relação-de-patrimonio",
    onAfterPrint: () => {
      toast.success("Impressão realizada com sucesso.");
    },
  });

  const selectableRows = {
    text: {
      checked: "Linha selecionada",
      unchecked: "Selecionar linha",
    },
  };

  return (
    <Layout>
      <section className="content-wrapper p-1">
        <div className="row d-flex align-items-center justify-content-center vh-100">
          <div className="col-6 mx-3 mb-5">
            <div className="card" ref={componentRef}>
              <div className="card-header">
                <div className="card-tools m-2">
                  <div className="input-group input-group-md">
                    <button
                      className="btn btn-sm btn-primary mr-2"
                      onClick={handlePrint}
                    >
                      <i className="fas fa-print" />
                    </button>
                    <input
                      type="text"
                      name="table_search"
                      className="form-control float-right"
                      placeholder="Search"
                      onChange={handleFilter}
                    />
                    <div className="input-group-append">
                      <button type="submit" className="btn btn-default">
                        <i className="fas fa-search" />
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div className="card-body table-responsive p-1 mb-3">
                <DataTable
                  title="Patrimonio"
                  lengthChange={true}
                  info={true}
                  striped={true}
                  autoWidth={true}
                  columns={columns}
                  data={records}
                  pagination={true}
                  theme={`${isDarkMode ? "dark" : "light"}`}
                  responsive={true}
                  fixedHeader={true}
                  paginationPerPage={5}
                  paginationRowsPerPageOptions={[5, 10, 15, 20, 25, 30]}
                  highlightOnHover={true}
                  customStyles={customStyles}
                  onSelectedRowsChange={handleRowSelected}
                  noDataComponent={<span>Nenhum registro encontrado.</span>}
                  paginationComponentOptions={{
                    rowsPerPageText: "Linhas por página:",
                    rangeSeparatorText: "de",
                    noRowsPerPage: false,
                    selectAllRowsItem: false,
                    selectAllRowsItemText: "Todos",
                  }}
                />
              </div>
            </div>
          </div>
        </div>
      </section>
      <Modal
        onCancel={() => setIsModalOpen(false)}
        footer={null}
        open={isModalOpen}
      >
        <EditCategory
          value={updateName}
          setValue={setUpdateName}
          handleSubmit={handleUpdate}
        />
      </Modal>
    </Layout>
  );
};

export default CategoryForm;
