import React, { useState, useEffect, useRef } from "react";
import { useAuth } from "../../context/auth";
import Layout from "../../components/Layout/Layout";
import DataTable, { createTheme } from "react-data-table-component";
import axios from "axios";
import { toast } from "react-toastify";
import { useReactToPrint } from "react-to-print";
import { useDarkMode } from "../../context/DarkModeContext";

const Patrimony = () => {
  const componentRef = useRef();
  const [auth, setAuth] = useAuth();
  const { isDarkMode } = useDarkMode();

  // States
  const [selectedRows, setSelectedRows] = useState([]);
  const [records, setRecords] = useState([]); //table data
  const [filterRecords, setFilterRecords] = useState([]); //table data

  // Função para carregar todos os patrimonios
  const getAllPatrimony = async () => {
    try {
      const { data } = await axios.get(`/api/patrimony/get-patrimony`);
      if (data.success) {
        setRecords(data?.patrimony);
        setFilterRecords(data?.patrimony);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getAllPatrimony();
  }, []);

  // Função para formatar o preço
  const formatarPreco = (preco) => {
    return preco.toLocaleString("pt-BR", {
      style: "currency",
      currency: "BRL",
    });
  };

  // Função para definir a tabela
  const columns = [
    {
      name: "Categoria",
      selector: (row) => row.category.name,
      sortable: true,
      width: "200px",
    },
    {
      name: "Descrição",
      selector: (row) => row.description,
      sortable: true,
      width: "450px",
    },
    {
      name: "Data-NF",
      selector: (row) => row.dtNota,
      sortable: true,
      width: "150px",
    },
    {
      name: "NF",
      selector: (row) => row.nota,
      sortable: true,
      width: "150px",
    },
    {
      name: "Preço",
      selector: (row) => row.vTotal,
      cell: (row) => formatarPreco(row.vTotal),
      sortable: true,
      width: "150px",
    },
    {
      name: "Ações",
      cell: (row) => (
        <a
          href={
            auth?.user?.role === 1
              ? `/dashboard/admin/patrimony/${row._id}`
              : `/dashboard/user/patrimony/${row._id}`
          }
        >
          <button className="btn btn-sm btn-success">
            <i className="fas fa-edit" />
          </button>
        </a>
      ),
      button: true,
      width: "80px",
    },
  ];

  // Função tema da tabela
  createTheme("dark", {
    text: {
      primary: "#fff",
      secondary: "#fff",
    },
    background: {
      default: "#292e33",
    },
    context: {
      background: "#3e4854",
      text: "#FFFFFF",
    },
    divider: {
      default: "#fff",
    },
    striped: {
      default: "#343a40",
      text: "#FFFFFF",
    },
    action: {
      button: "rgba(0,0,0,.54)",
      hover: "rgba(0,0,0,.08)",
      disabled: "rgba(0,0,0,.12)",
    },
  });

  // Função para customizar a tabela
  const customStyles = {
    rows: {
      style: {
        // minHeight: "72px", // override the row height
      },
    },
    headCells: {
      style: {
        textTransform: "uppercase",
        fontSize: "18px",
        fontWeight: "bold",
      },
    },
    cells: {
      style: {
        textTransform: "uppercase",
        fontSize: "15px",
      },
    },
  };

  const handleFilter = (e) => {
    const searchTerm = e.target.value.toLowerCase();

    const newData = filterRecords.filter(
      (row) =>
        row.category.name.toLowerCase().includes(searchTerm) ||
        row.description.toLowerCase().includes(searchTerm)
    );

    setRecords(newData);
  };

  // Função para selecionar as linhas
  const handleRowSelected = (state) => {
    setSelectedRows(state.selectedRows);
  };

  // Função para calcular o total
  const calculateTotal = () => {
    const total = selectedRows.reduce((acc, row) => acc + row.vTotal, 0);
    return total;
  };

  // Função para imprimir
  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
    documentTitle: "relação-de-patrimonio",
    onAfterPrint: () => {
      toast.success("Impressão realizada com sucesso.");
    },
  });

  const selectableRows = {
    text: {
      checked: "Linha selecionada",
      unchecked: "Selecionar linha",
    },
  };

  return (
    <Layout>
      <section className="content-wrapper p-3">
        <div className="row d-flex align-items-center justify-content-center vh-100">
          <div className="col-12">
            <div
              className="card"
              ref={componentRef}
            >
              <div className="card-header">
                <div className="card-tools">
                  <div
                    className="input-group input-group-md"
                  >
                    <button
                      className="btn btn-sm btn-primary mr-5"
                      onClick={handlePrint}
                    >
                      <i className="fas fa-print" />
                    </button>
                    <input
                      type="text"
                      name="table_search"
                      className="form-control float-right"
                      placeholder="Search"
                      onChange={handleFilter}
                    />
                    <div className="input-group-append">
                      <button type="submit" className="btn btn-default">
                        <i className="fas fa-search" />
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div>
                <DataTable
                  title="Patrimonio"
                  lengthChange={true}
                  info={true}
                  striped={true}
                  autoWidth={true}
                  columns={columns}
                  data={records}
                  pagination={true}
                  theme={`${isDarkMode ? "dark" : "light"}`}
                  responsive={true}
                  selectableRows={selectableRows}
                  selectableRowsHighlight={true}
                  fixedHeader={true}
                  paginationPerPage={5}
                  paginationRowsPerPageOptions={[5, 10, 15, 20, 25, 30]}
                  highlightOnHover={true}
                  customStyles={customStyles}
                  onSelectedRowsChange={handleRowSelected}
                  noDataComponent={<span>Nenhum registro encontrado.</span>}
                  paginationComponentOptions={{
                    rowsPerPageText: "Linhas por página:",
                    rangeSeparatorText: "de",
                    noRowsPerPage: false,
                    selectAllRowsItem: false,
                    selectAllRowsItemText: "Todos",
                  }}
                />
                <div>
                  <h5 className="col-13 m-3">
                    Valor total: {formatarPreco(calculateTotal())}
                  </h5>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  );
};

export default Patrimony;
