import React, { useState, useEffect } from "react";
import Layout from "../../components/Layout/Layout";
import axios from "axios";
import { toast } from "react-toastify";
import { useNavigate } from "react-router-dom";

const CreatePatrimony = () => {
  const navigate = useNavigate();

  const [categories, setCategories] = useState([]);
  const [category, setCategory] = useState("");
  const [description, setDescription] = useState("");
  const [dtNota, setDtNota] = useState("");
  const [nota, setNota] = useState("");
  const [location, setLocation] = useState("Não informado");
  const [identifier, setIdentifier] = useState("");
  const [financiado, setFinanciado] = useState("");
  const [vTotal, setVTotal] = useState("");
  const [vPago, setVPago] = useState(0);
  const [vAPagar, setVAPagar] = useState(0);

  // get all categories
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get(`/api/category/get-category`);
      if (data?.success) {
        setCategories(data?.category);
      }
    } catch (error) {
      console.log(error);
      toast.error("Erro ao carregar categorias.");
    }
  };

  useEffect(() => {
    getAllCategory();
  }, []);

  const handleCreatePatrimony = async (e) => {
    e.preventDefault();
    try {
      // cria um novo patrimonio
      const patrimonyData = new FormData();
      patrimonyData.append("category", category);
      patrimonyData.append("description", description);
      patrimonyData.append("dtNota", dtNota);
      patrimonyData.append("nota", nota);
      patrimonyData.append("identifier", identifier);
      patrimonyData.append("location", location);
      patrimonyData.append("financiado", financiado);
      patrimonyData.append("vTotal", vTotal);
      patrimonyData.append("vPago", vPago);
      patrimonyData.append("vAPagar", vAPagar);

      const { data } = await axios.post(
        `/api/patrimony/create-patrimony`,
        patrimonyData
      );

      if (data?.success) {
        toast.success("Patrimonio criado com sucesso.");
      
      // Limpa os campos para um novo cadastro
      setCategory("");
      setDescription("");
      setDtNota("");
      setNota("");
      setLocation("Não informado");
      setIdentifier("");
      setFinanciado("");
      setVTotal("");
      setVPago(0);
      setVAPagar(0);

        navigate("/dashboard/admin/create-patrimony");
      } else {
        toast.error("Erro ao criar patrimonio.");
      }
    } catch (error) {
      console.log(error);
      toast.error("Erro ao criar patrimonio.");
    }
  };

  return (
    <Layout>
      <div className="content-wrapper p-5">
        <div className="container-fluid">
          <div className="card col-12 m-2">
            <div className="card-body card-body">
              <h5 className="ext-uppercase">Cadastro</h5>
              <form>
                <div className="row">
                  <div className="col-6">
                    <label className="text-uppercase">Categoria</label>
                    <div className="form-group mb-2">
                      <select
                        className="form-control text-uppercase"
                        name="category"
                        value={category}
                        onChange={(e) => setCategory(e.target.value)}
                      >
                        <option>Selecione uma categoria</option>
                        {categories.map((category) => (
                          <option key={category._id} value={category._id}>
                            {category.name}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                  <div className="col-6">
                    <label className="text-uppercase">Identificador</label>
                    <div className="input-group mb-2">
                      <select
                        type="text"
                        name="identifier"
                        value={identifier}
                        onChange={(e) => setIdentifier(e.target.value)}
                        className="form-control text-uppercase"
                        placeholder="Identificador"
                        required
                      >
                        <option>Selecione um nome</option>
                        <option>Bruno Rubin Stefanello</option>
                        <option>Ana Rúbia Costa da Cunha</option>
                      </select>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-6">
                    <label className="text-uppercase">Data da nota</label>
                    <div className="input-group mb-2">
                      <input
                        type="date"
                        name="dtNota"
                        value={dtNota}
                        onChange={(e) => setDtNota(e.target.value)}
                        className="form-control text-uppercase"
                        placeholder="Data da nota fiscal"
                        required
                      />
                    </div>
                  </div>
                  <div className="col-6">
                    <label className="text-uppercase">Nota fiscal</label>
                    <div className="input-group mb-2">
                      <input
                        type="number"
                        name="nota"
                        value={nota}
                        onChange={(e) => setNota(e.target.value)}
                        className="form-control text-uppercase"
                        placeholder="Nota fiscal"
                        required
                      />
                    </div>
                  </div>
                </div>
                <label className="text-uppercase">Descrição</label>
                <div className="input-group mb-2">
                  <input
                    type="text"
                    name="description"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    className="form-control text-uppercase"
                    placeholder="descrição"
                    required
                  />
                </div>
                <label className="text-uppercase">Localização</label>
                <div className="input-group mb-2">
                  <input
                    type="text"
                    name="location"
                    value={location}
                    onChange={(e) => setLocation(e.target.value)}
                    className="form-control text-uppercase"
                    placeholder="Localização"
                    required
                  />
                </div>
                <label className="text-uppercase">Financiado</label>
                <div className="input-group mb-2">
                  <select
                    type="text"
                    name="financiado"
                    value={financiado}
                    onChange={(e) => setFinanciado(e.target.value)}
                    className="form-control text-uppercase"
                    placeholder="O bem é financiado?"
                    required
                  >
                    <option>O bem é financiado?</option>
                    <option>sim</option>
                    <option>não</option>
                  </select>
                </div>
                <label className="text-uppercase">Valor pago</label>
                <div className="input-group mb-2">
                  <input
                    type="number"
                    name="vPago"
                    value={vPago}
                    onChange={(e) => setVPago(e.target.value)}
                    className="form-control text-uppercase"
                    placeholder="Valor pago"
                    required
                  />
                </div>
                <label className="text-uppercase">Valor a pagar</label>
                <div className="input-group mb-2">
                  <input
                    type="number"
                    name="vAPagar"
                    value={vAPagar}
                    onChange={(e) => setVAPagar(e.target.value)}
                    className="form-control text-uppercase"
                    placeholder="Valor a pagar"
                    required
                  />
                </div>
                <label className="text-uppercase">Valor total</label>
                <div className="input-group mb-2">
                  <input
                    type="number"
                    name="vTotal"
                    value={vTotal}
                    onChange={(e) => setVTotal(e.target.value)}
                    className="form-control text-uppercase"
                    placeholder="Valor total"
                    required
                  />
                </div>
                <div className="row">
                  <div className="col-6">
                    <button
                      type="submit"
                      className="btn btn-success btn-block text-uppercase"
                      onClick={handleCreatePatrimony}
                    >
                      Salvar
                    </button>
                  </div>
                  <div className="col-6">
                    <button
                      type="submit"
                      className="btn btn-warning btn-block text-uppercase"
                      onClick={() => navigate("/dashboard/admin/patrimony")}
                    >
                      Sair
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default CreatePatrimony;
